.container {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
  max-width: 1140px;
}

.container--small {
  max-width: 960px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

a {
  text-decoration: none;
  color: #faac09;
}

a:hover,
a:active {
  text-decoration: underline;
}

img {
  max-width: 100%;
  height: auto;
}

.jc-space-between {
  justify-content: space-between;
}

.ai-center {
  align-items: center;
}

.text-yellow {
  color: #faac09;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.front-page {
  background-image: url("../img/main-bg.png");
  background-position: center left;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #999999;
}

.page-title {
  text-transform: uppercase;
  font-size: 1.75rem;
  font-weight: bold;
  line-height: 1.2;
  position: relative;
  margin-bottom: 1.75rem;
  overflow: hidden;
}

@media only screen and (min-width: 900px) {
  .page-title {
    margin-bottom: 3.75rem;
    font-size: 2.75rem;
  }
}

@media only screen and (min-width: 1140px) {
  .page-title {
    overflow: visible;
    font-size: 3.75rem;
  }
}

.page-title__text {
  position: relative;
  padding: 2.5rem 0;
  display: inline-block;
  z-index: 100;
}

@media only screen and (min-width: 900px) {
  .page-title__text {
    padding: 3.5rem 0;
  }
}

.page-title__bg {
  z-index: 10;
  position: absolute;
  font-size: 2.8em;
  line-height: 1;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #f6f6f6;
}

@media only screen and (min-width: 1400px) {
  .page-title__bg {
    left: -12%;
  }
}

.page-title__bg:before {
  position: absolute;
  content: '';
  height: 20px;
  width: 100%;
  left: -105%;
  top: 0;
  bottom: 0;
  margin: auto;
  background-color: #faac09;
}

.grid {
  min-height: 100vh;
}

.grid-item {
  will-change: left, top, transfrom, opacity;
}

.progress-bar {
  padding: 50px 0;
  margin-bottom: 1.75rem;
  display: flex;
  align-items: center;
}

@media only screen and (min-width: 900px) {
  .progress-bar {
    margin-bottom: 3.75rem;
  }
}

.progress-bar__row {
  display: flex;
  flex: 1;
  justify-content: space-between;
  position: relative;
  margin: 0 20px;
}

.progress-bar__row:before {
  position: absolute;
  content: '';
  height: 0;
  border: 4px solid #faac09;
  top: 0;
  bottom: 0;
  left: 10px;
  right: 10px;
  margin: auto;
  z-index: -2;
}

.progress-bar__item {
  flex: 1;
  justify-content: flex-end;
  position: relative;
  display: flex;
}

.progress-bar__item:first-child {
  flex: 0;
}

.progress-bar__item.is-active ~ .progress-bar__item .progress-bar__btn {
  background-color: #aaa;
  border-color: #aaa;
}

.progress-bar__item.is-active ~ .progress-bar__item .progress-bar__btn:after {
  border-color: #aaa;
}

.progress-bar__item.is-active ~ .progress-bar__item:before {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 2px;
  z-index: 10;
  content: '';
  width: 100%;
  margin: auto;
  height: 0;
  border: 5px solid #f6f6f6;
  z-index: -1;
}

@media only screen and (min-width: 1200px) {
  .progress-bar__item.is-active ~ .progress-bar__item:before {
    border: 5px solid #f6f6f6;
  }
}

.progress-bar__item:last-child .progress-bar__date, .progress-bar__item:last-child .progress-bar__event {
  transform: none;
}

.progress-bar__btn {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #333;
  position: relative;
  border: 2px solid #333;
}

.progress-bar__btn:after {
  position: absolute;
  content: '';
  border: 2px solid #333;
  left: -50%;
  top: -50%;
  margin: auto;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transform: scale(0);
  transition: transform 0.3s cubic-bezier(0.09, 0.26, 0.52, 1.95);
}

.progress-bar__btn.is-checked:after {
  transform: scale(1);
}

.progress-bar__event {
  position: absolute;
  right: 0;
  transform: translateX(calc(100% - 24px));
  bottom: 150%;
  font-size: .875rem;
  font-weight: bold;
  max-width: 90px;
}

.progress-bar__date {
  position: absolute;
  right: 0;
  transform: translateX(calc(100% - 24px));
  top: 150%;
  font-weight: bold;
  font-size: 0.875rem;
  color: #aaa;
}

.grid-item {
  margin-bottom: 30px;
}

.grid-item,
.grid-sizer {
  width: 100%;
}

@media only screen and (min-width: 600px) {
  .grid-item,
  .grid-sizer {
    width: 48%;
  }
}

@media only screen and (min-width: 1000px) {
  .grid-item,
  .grid-sizer {
    width: 32%;
  }
}

.gutter-sizer {
  width: 2%;
}

.card {
  background-color: #fff;
  box-shadow: 0px 3px 6px 0px rgba(0, 11, 10, 0.05);
  transition: box-shadow .3s ease-in-out;
}

.card__header {
  padding-bottom: 1rem;
  padding-top: 2rem;
}

.card__header-text {
  display: inline-block;
  width: 72%;
  padding: 0 1.875rem;
  background-color: #faac03;
  color: #fff;
  font-size: .875rem;
  font-weight: 700;
  text-transform: uppercase;
}

.card__body {
  padding: 1.5rem 1.875rem;
  padding-top: 0;
}

.card__body-title {
  color: #333333;
  font-weight: 700;
  font-size: 1.375rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.card__body-text {
  color: #868686;
  font-weight: 300;
  font-size: 1rem;
}

.card:hover {
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.3);
}

.grid-item.done .card__header-text {
  background-color: #aaa;
}

