.btn {
  display: inline-block;
  font-weight: 700;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 2px solid #efefef;
  padding: .575rem 1.625rem;
  color: #333;
  background-color: transparent;
  font-size: 0.875rem;
  text-transform: uppercase;
  line-height: 1.5;
  transition: all .15s;
}

.btn:hover {
  text-decoration: none;
}

.btn--primary {
  background-color: rgba(51, 51, 51, 0.75);
}

.btn--primary:hover {
  background-color: #333333;
}

.btn--secondary {
  background-color: rgba(250, 172, 9, 0.75);
}

.btn--secondary:hover {
  background-color: #faac09;
}

.btn--transition {
  position: relative;
}

.btn--transition:after {
  content: '';
  position: absolute;
  top: -2px;
  right: -2px;
  width: 50%;
  height: 50%;
  border-top: 2px solid #faac09;
  border-right: 2px solid #faac09;
  transition: .3s;
}

.btn--transition:before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: -2px;
  width: 50%;
  height: 50%;
  border-bottom: 2px solid #faac09;
  border-left: 2px solid #faac09;
  transition: .2s;
}

.btn--transition:hover:before {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
}

.btn--transition:hover:after {
  width: calc(100% + 4px);
  height: calc(100% + 4px);
}

.btn--transition-simple {
  position: relative;
  border: none;
  padding: .75rem 1rem;
  font-size: 1rem;
}

.btn--transition-simple:before {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: .9rem;
  right: .9rem;
  border-top: 2px solid #faac09;
  transform-origin: left;
  transform: scaleX(0.5);
  transition: transform .3s ease-in-out;
}

.btn--transition-simple:after {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: .9rem;
  right: .9rem;
  border-bottom: 2px solid #faac09;
  transform-origin: right;
  transform: scaleX(0.5);
  transition: transform .3s ease-in-out;
}

.btn--transition-simple:hover:before {
  transform: scaleX(1);
}

.btn--transition-simple:hover:after {
  transform: scaleX(1);
}

.btn--block {
  width: 100%;
